import React from "react";
import { Route, BrowserRouter, Redirect } from "react-router-dom";
import { useStoreState, useStoreActions } from "easy-peasy";

import Auth from "../Auth/Auth";
import Settings from "./Settings/Settings";
import MyInfo from "./MyInfo/MyInfo";
import Activities from "./Activities/Activities";

function Main() {
  let setAuth = useStoreActions(action => action.auth.setAuth);
  let setProfile = useStoreActions(action => action.profile.setProfile);

  function clearProfile() {
    setProfile({
      type: "U",
      isLoading: true,
      fname: "",
      lname: "",
      type: "U",
      dob: "",
      sex: "",
      phone: "",
      sem: "",
      domainId: "",
      address_line1: "",
      address_line2: "",
      city: "",
      dist: "",
      state: "",
      pin: "",
      branch: "",
      aboutMe: "",
      email: "",
      skills: [],
      isDone: false,
      attendance: "",
      aboutme: ""
    });
  }

  return (
    <section id="dashboard" class="disp-flex">
      <BrowserRouter>
        <Route exact path="/" component={Auth} />
        <Route
          path="/logout"
          render={() => {
            localStorage.clear();
            setAuth(false);
            clearProfile();
            return <Redirect to="/" />;
          }}
        />
        <ProtectedRoute path="/settings" component={Settings} />
        <ProtectedRoute path="/myinfo" component={MyInfo} />
        <ProtectedRoute path="/activities" component={Activities} />
      </BrowserRouter>
    </section>
  );
}

function ProtectedRoute({ component: Component, ...rest }) {
  let isAuth = useStoreState(state => state.auth.isAuth);
  if (isAuth) {
    return <Route {...rest} component={Component} />;
  } else {
    return <Redirect to="/" />;
  }
}

export default Main;
